<template>
    <table-component
            ref="table"
            @handleOperate="handleOperate"
            :columns="columns"
            :request="tableRequest">
    </table-component>
</template>

<script>
    import {getUserExamResultList} from '@/api/examResult'
    import tableComponent from '@/components/table'

    export default {
        components: {
            tableComponent
        },
        data() {
            return {
                columns: [
                    {
                        title: '#',
                        dataIndex: 'index',
                        scopedSlots: {customRender: 'index'},
                    },
                    {
                        title: '考试信息',
                        dataIndex: 'paper.name',
                        key: 'paper.name',
                    },
                    {
                        title: '满分',
                        dataIndex: 'paper.max_score',
                        key: 'paper.max_score',
                    },
                    {
                        title: '及格分',
                        dataIndex: 'paper.pass_score',
                        key: 'paper.pass_score',
                    },
                    {
                        title: '得分',
                        dataIndex: 'score',
                        key: 'score',
                    },
                    {
                        title: '结果',
                        dataIndex: 'is_passed_format',
                        key: 'is_passed_format',
                    },
                    {
                        title: '考试时间',
                        dataIndex: 'created_at_format',
                        key: 'created_at_format',
                    },
                    {
                        title: '交卷时间',
                        dataIndex: 'updated_at_format',
                        key: 'updated_at_format',
                    },
                    {
                        title: '操作',
                        dataIndex: 'operation',
                        scopedSlots: {customRender: 'operation'},
                        buttons: {
                            check: {visible: true, disable: false,},
                        }
                    }
                ],
                tableRequest: getUserExamResultList,
            }
        },
        methods: {
            handleSearch(filter) {
                this.$refs.table.handleSearch(filter)
            },
            handleCheck(id) {
                this.$router.push({name: '在线考试查看', params: {id: id}})
            },
            handleOperate(type, row) {
                switch (type) {
                    case 'check':
                        this.handleCheck(row.id);
                        break;
                }
            },
        },
    }
</script>
